import React from "react";
import Tableau from "tableau-react";

const About = () => {
  const options = {
    hideTabs: true,
    hideToolbar: true
  };

  return (
    <div>
      <h1>
        World
      </h1>

      <Tableau
        url="https://public.tableau.com/views/WorldCovidVizv1_2_0/WorldDashboard?:language=en-US&:display_count=n&:origin=viz_share_link"
        options={options}
      />

        <p>
          Dashboard Source{" "}
          <a href="https://public.tableau.com/app/profile/sam.prewitt/viz/WorldCovidVizv1_2_0/WorldDashboard">
            Covid-19 example viz
          </a>
        </p>

        

    </div>
  );
};
  
export default About;